@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{  
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.profile,
.fitness {
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.link { text-decoration: none; color: black;}
.link:visited { text-decoration: none;  color: black; }
.link:hover { text-decoration: none;  color: black; }
.link:focus { text-decoration: none;   color: black;}
.link:hover, a:active { text-decoration: none;  color: black;}